export default class mainView extends Vue {
    code:any = null;
    addSomeHouseFlag:boolean=false
    rdId: string = ''
    roomName: any = ''
    unitInfo: any = []
    roomType: any = []
    roomHead: any = []
    editRoom: any = {
        buildArea: 0,
        buildingId: "",
        floorId: "",
        name: "",
        rdId: "",
        roomDetail: {
            roomTypeCode: "",
            roomTowardsCode: "",
            roomStatusCode: "",
            roomConstructCode: "",
            roomUsageTypeCode: ""
        },
        roomNum: "",
        useArea: ''
    }
    roomConstruct: any = []
    roomUsageType: any = []
    roomStatus: any = []
    editRoomVisible: boolean = false
    buildAreaStart: string = null
    buildAreaEnd: string = null
    keyWords: string = null
    unitSelect: string = null
    roomTowards: string = null
    roomStatusSelect: string = null
    buildingName: string = ''
    floorList:any=[]
    editFloorNum:string=''
    addRoomInfo: any = {
        buildArea: '',
        buildingId: "",
        floorNum: "",
        name: "",
        rdId: "",
        roomDetail: {
            roomTypeCode: "",
            roomTowardsCode: "",
            roomStatusCode: "",
            roomConstructCode: "",
            roomUsageTypeCode: ""
        },
        roomNum: "",
        roomPicId: "",
        unitId: '',
        useArea: ''
    }
    roomId: string = '';
